* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #000;
  background-color: violet;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 8px;
}

.loader {
  width: 12px;
  height: 12px;
  color: #fff;
  box-sizing: border-box;
  border-radius: 50%;
  margin: 15px auto;
  animation: animloader 2s linear infinite;
  display: block;
  position: relative;
}

@keyframes animloader {
  0% {
    box-shadow: 14px 0 0 -2px, 38px 0 0 -2px, -14px 0 0 -2px, -38px 0 0 -2px;
  }

  25% {
    box-shadow: 14px 0 0 -2px, 38px 0 0 -2px, -14px 0 0 -2px, -38px 0 0 2px;
  }

  50% {
    box-shadow: 14px 0 0 -2px, 38px 0 0 -2px, -14px 0 0 2px, -38px 0 0 -2px;
  }

  75% {
    box-shadow: 14px 0 0 2px, 38px 0 0 -2px, -14px 0 0 -2px, -38px 0 0 -2px;
  }

  100% {
    box-shadow: 14px 0 0 -2px, 38px 0 0 2px, -14px 0 0 -2px, -38px 0 0 -2px;
  }
}

.breed-select {
  color: #202022;
  background-color: #f2f4ec;
  border: 1px solid #b41b81;
  border-radius: 5px;
  padding: 5px;
  font-size: 18px;
}

.breed-select:focus {
  outline: 2px solid #b41b81;
}

.cat-info {
  flex-direction: column;
  margin-top: 20px;
  display: flex;
}

.cat-img {
  width: 100%;
  height: 100%;
}

.cat-text {
  border: 2px solid #464da8;
  border-radius: 20px;
  margin-top: 20px;
  padding: 20px;
}

.hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

@media screen and (min-width: 480px) {
  .container {
    max-width: 428px;
    margin-left: auto;
    margin-right: auto;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }

  .cat-img {
    min-height: 100%;
  }

  .cat-info {
    flex-direction: row;
    align-items: center;
  }

  .cat-img {
    width: 400px;
  }

  .cat-text {
    border: none;
    flex-grow: 1;
    margin-top: 0;
    margin-left: 20px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
  }

  .cat-info {
    flex-direction: row;
    align-items: normal;
  }

  .cat-img {
    width: 600px;
  }
}

